import React, { useState } from 'react';
import { Text } from 'react-native';
import useIsTestChannel from '..';

// 本Demo展示了如何使用useIsTestChannel判断当前App是否是测试版
export default () => {
    const [isTest, setIsTest] = useState(false);
    useIsTestChannel()
        .then((result: boolean) => setIsTest(result))
        .catch((e) => console.log(e));
    return <Text>当前APP是否是测试版：{isTest ? 'true' : 'false'}</Text>;
};
